import React, { useState, useLayoutEffect } from 'react';
import { NavHashLink as NavLink } from 'react-router-hash-link';


function Navbar() {
   let [worksActive, setWorksActive] = useState("");
   let [skillsActive, setSkillsActive] = useState("");
   let [aboutActive, setAboutActive] = useState("");

   useLayoutEffect(() => {
      let activeWorks = (e) => setWorksActive(e);
      let activeSkills = (e) => setSkillsActive(e);
      let activeAbout = (e) => setAboutActive(e);
      let works = document.querySelector("#works");
      let worksHeight = parseFloat(window.getComputedStyle(works).getPropertyValue("height")) - 200;
      let skills = document.querySelector("#skills");
      let skillsHeight = parseFloat(window.getComputedStyle(skills).getPropertyValue("height")) + worksHeight;
      let about = document.querySelector("#about");
      let aboutHeight = parseFloat(window.getComputedStyle(about).getPropertyValue("height")) + skillsHeight;

      let onScroll = () => {
         let scrollPos = window.scrollY;
         let w = window.innerWidth;

         if (w > 750) {
            if (scrollPos >= worksHeight && scrollPos < skillsHeight) {
               activeWorks("selected");
               activeSkills("");
               activeAbout("");
            }
            if (scrollPos >= skillsHeight && scrollPos < aboutHeight) {
               activeWorks("");
               activeSkills("selected");
               activeAbout("");
            }
            if (scrollPos >= aboutHeight) {
               activeWorks("");
               activeSkills("");
               activeAbout("selected");
            }
         }
         
         
         
      }

      window.addEventListener("scroll", onScroll);
      return () => window.removeEventListener("scroll", onScroll);
   }, [])

   return (
      <React.Fragment>
         <div className="navWrap d-flex w-100 px-md-5 px-2" id="navID" >
            <ul className="myNav py-2 m-0 firstFont">
               <NavLink to="/#works"  className="navLink" activeClassName={worksActive}>
                  <li className="listItem mx-4">
                     Works
                  </li>
               </NavLink>
               <NavLink to="/#skills" className="navLink" activeClassName={skillsActive}>
                  <li className="listItem mx-4">
                     Skills
                  </li>
               </NavLink>
               <NavLink to="/#about" className="navLink" activeClassName={aboutActive}>
                  <li className="listItem mx-4">
                     About
                  </li>
               </NavLink>
            </ul>
         </div>
      </React.Fragment>
   )
}

export default Navbar;